.social__list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  padding: 0;
}

.social__item {
  border: 3px solid rgba(40, 54, 69, 0.1);
  border-radius: 50%;
}

.social__link {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
}
